import React, { useEffect, useState } from 'react'
import Table from './Table'
//import Form from './Form'

const DOMAIN = "memohat.xyz"

function App() {
    const [status, setStatus] = useState(null)
    const [isLoading, setLoading] = useState(true)

    useEffect(() => {async function getStatus() {
        let res = await fetch("https://mcapi.us/server/query/" + DOMAIN)
        let res_json = await res.json()
        if (res_json.online) {
            res = await fetch("https://mcapi.us/server/status/" + DOMAIN)
            res_json = await res.json()
        }
        setStatus(res_json)
        setLoading(false)
    }
    getStatus()
    },[isLoading])

    if (!status || !status.online) {
        return <div className="container">
            <h1>Status: {isLoading ? "Loading..." : "Down"}</h1>
            <button onClick={() => setLoading(true)}>
                refresh
        </button>
        </div>
    }
    return <div className="container">
        <h1>Status: {isLoading ? "Loading..." : "Running"}</h1>
        <h1>MOTD: {status.motd}</h1>
        <h1>Version: {status.server.name}</h1>
        <h1>Users: {status.players.now}/{status.players.max}</h1>
        <Table users={status.players.sample} />
        <button onClick={() => setLoading(true)}>
            refresh
        </button>
    </div>
}

export default App
